import React from 'react'
import Layout from '../../containers/Layout'
import H2 from '../../components/Headings/H2'
import H4 from '../../components/Headings/H4'
import Lead from '../../components/Text/Lead'
import P from '../../components/Text/P'
import CodeCard from '../../components/CodeCard'
import ExampleSection from '../../components/ExampleSection'

const dropdownLeftCode = `
<div class="relative">
  <button class="rounded-lg px-5 h-10 text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-800 transition-colors duration-150 inline-flex items-center">
    <span>With icon</span>
    <svg class="fill-current h-4 w-4 ml-3" viewBox="0 0 20 20"><path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clip-rule="evenodd" fill-rule="evenodd"></path></svg>
  </button>
  <div class="absolute mt-1 rounded-lg flex py-2 flex-col bg-white text-gray-700">
    <a class="px-2 hover:text-gray-800 focus:bg-indigo-500" href="#">Link item 1</a>
    <a class="px-2 hover:text-gray-800 focus:bg-indigo-500" href="#">Link item 2</a>
    <a class="px-2 hover:text-gray-800 focus:bg-indigo-500" href="#">Link item 3</a>
  </div>
</div>
`

function Dropdowns() {
  return (
    <Layout>
      <H2>Dropdowns</H2>
      <Lead>Dropdown components using Tailwind CSS to speed up your project.</Lead>
      <hr className="my-8" />

      <ExampleSection id="button-sizes">
        <H4>Dropdown left aligned</H4>
        <P>Dropdown button with left aligned menu.</P>
        <CodeCard lang="html" code={dropdownLeftCode} />
      </ExampleSection>
    </Layout>
  )
}

export default Dropdowns
